body {
  background-color: #CB2D3E;
  
}
#app-container{
  display: grid;
  grid-template-columns: 1fr 6fr 1fr;
  grid-template-rows: 100% 780px; 
  grid-row-gap: 70px;  
}


/* --------- LOADING STAGE ---------*/


#loading-page{
  background-color: #212121;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 3fr 1fr 1fr 1fr 2fr;
  grid-column-start: 2;
  grid-row-start: 2;
}
#first-loader {
  --size : 50px;
  margin: auto;
  border: 8px solid #323232; /* Light grey */
  border-top: 8px solid #E7413B; /* Blue */
  border-radius: 50%;
  width: var(--size);
  height: var(--size);
  -webkit-animation: spin 2.5s linear infinite;
          animation: spin 2.5s linear infinite;
  grid-row-start: 4;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
} 
#title {
  color: white;
  margin: 0;
  font-size: 72px;
  text-align: center;
  font-family: 'Lato';
  grid-row-start: 2;
}
#subtitle {
  color: white;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 75px;
  text-align: center;
  font-size: 30px;
  font-family: 'Lato light';
  font-style: italic;
  grid-row-start: 3;
}


/* --------- LOADED STAGE ---------*/


#app {
  background-color: #212121;
  border-radius: 10px;
  display: none;
  grid-template-columns: 1.2fr 2fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-column-start: 2;
  grid-row-start: 2;
}
#decoration {
  display: grid;
  grid-template-rows: 1fr 1fr;
  grid-row-start: 2
}
#deco-title {
  color: white;
  margin: 0;
  font-size: 60px;
  text-align: center;
  font-family: 'Lato';
  margin-top: 20px;
}
#deco-subtitle {
  color: white;
  margin: 0;
  text-align: center;
  font-size: 25px;
  font-family: 'Lato light';
  font-style: italic;
  grid-row-start: 2;
}
#user {
  background-color: #323232;
  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: 4;
  display: grid;
  grid-template-rows: 1fr 4.5fr 
}



