#currentWeather{
  display: grid;
  grid-template-rows: 1fr
}
#loader {
  display: none;
  --size : 60px;
  /* margin: 100px; */
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: 330px;
  border: 8px solid #212121; /* Light grey */
  border-top: 8px solid #E7413B; /* Blue */
  border-radius: 50%;
  width: var(--size);
  height: var(--size);
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  /* grid-row-start: 2; */
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
#container {
  display: grid;
  grid-template-rows: 1fr 2fr 3fr;
  grid-template-columns: 1fr 1fr 1fr
}
#top {
  grid-row-start: 1;
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 68px 1fr;
  /* text-align: center; */

}
#location {
  font-family: 'Lato light';
  color: white;
  font-size: 50px;
  /* height: 68px; */
  text-align: center;
  margin: 0;
}
#sub-container{
  margin-right: auto;
  margin-left: auto;
  display: -ms-flexbox;
  display: flex;
}
#description {
  margin: 0;
  text-align: center;
  font-family: 'Lato light';
  font-style: italic;
  font-size: 28px;
  color: white;
  grid-row-start: 2;
  grid-column-start: 1;
}
#weather-icon {
  grid-row-start: 2;
  grid-column-start: 1;
  padding-right: 15px;
  filter: invert(100%);
  -webkit-filter: invert(100%);
  width: auto;
  height: 25px;
  margin-top: auto;
  margin-bottom: auto;
  
}
#weather-icon #description {
  display: inline;
}
#temp {
  grid-column-start: 2;
  grid-row-start: 2;
  font-family: 'Lato';
  margin: 0;
  font-size: 150px;
  color: white;
  text-align: center
}
#expo {
  font-family: 'Lato light';
  font-size: 40px;
  line-height: 90px;
  vertical-align: text-top;
}
#bottom {
  grid-row-start: 3;
  grid-column-start: 1;
  grid-column-end: 4;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr .5fr;
  text-align: center;
  font-family: 'Lato light';
  font-style: italic;
  color: white;
  
}
.param {
  margin: 0;
  margin-right: 15px;
  display: inline;
  color: #CD3E39;
  font-style: initial;
}
#temp-min {
  font-size: 25px;
  grid-row-start: 1;
  grid-column-start: 1;
}
#temp-max {
  font-size: 25px;
  grid-row-start: 1;
  grid-column-start: 2;
  
}
#wind-speed {
  font-size: 25px;
  grid-row-start: 2;
  grid-column-start: 1;
}
#wind-dir {
  font-size: 25px;
  grid-row-start: 2;
  grid-column-start: 2;
}
#compass {
  filter: invert(100%);
  -webkit-filter: invert(100%);
  vertical-align: bottom;
}
.margin {
  margin-right: 25px;
}
#humidity {
  font-size: 25px;
  grid-row-start: 3;
  
}
#clouds {
  font-size: 25px;
  grid-row-start: 3;

}


